<template>
	<div class="">
		<div class="frame">
			<div class="container" v-for="(item,index) in config">
				<Card class="card" :title="item.title" :path="item.path" :router="item.router"></Card>
			</div>
		</div>

		<div>
			<el-upload
		      drag
		      action="#"
		      :show-file-list="false"
		      :on-success="handleUploadSuccess"
		      :on-change="handleUploadChange"
		      :before-upload="beforeUpload"
		    >
		      <i class="el-icon-upload"></i>
		      <div class="el-upload__text"><em></em></div>
		      <div class="el-upload__tip" slot="tip"></div>
		    </el-upload>

			<el-dialog :visible.sync="dialogVisible" title="" @close="resetProgress">
				<div style="text-align: center;">
					<el-progress :percentage="progress" />
				</div>
			</el-dialog>
		</div>
	</div>

</template>

<script>
	import Card from './Card'
	import CardConfig from '@/assets/card/CardConfig.json'
	export default {
		name: "CardLayout",
		components: {
			Card
		},
		data() {
			return {
				config: CardConfig,

				dialogVisible: false,
			      progress: 0,
			      timer: null,
			      loadingTime: 200, // 30
			    upload_file : "GMM"
			}
		},
		methods: {
			beforeUpload(file) {
		      // 
		      this.upload_file = file.name.replace(/\.[^/.]+$/, "");;
		      // 
		      this.openDialog();
		      // 
		      return false;
		    },
			handleUploadSuccess(response, file) {
		      // 
		      this.upload_file = file.name;
		      console.log(this.upload_file)
		      this.openDialog();
		    },
		    handleUploadChange(file, fileList) {
		      // 
		      if (file.status === 'success') {
		        this.upload_file = file.name;
		      }
		    },
			openDialog() {
				this.dialogVisible = true;
				this.startProgress();
			},
			closeDialog() {
				this.dialogVisible = false;
				this.resetProgress();
			},
			startProgress() {
				this.progress = 0;
//				
				const interval = this.loadingTime;  
				this.timer = setInterval(() => {
					this.progress += Math.round(Math.random() * 10);
					if(this.progress >= 100) {
						clearInterval(this.timer);
						this.dialogVisible = false;
					}
				}, interval);
			},
			resetProgress() {
				clearInterval(this.timer);
				this.progress = 0;
				
				this.config.push(
					{
						"title":this.upload_file,
						"path": "./icons/Variable",
						"router": "/newpage"
					}
				)
			},
		},
	}
</script>

<style scoped>
	.frame {
		/*flex: inherit;*/
	}
	
	.container {
		/*width: ;*/
		display: inline-block;
		/**/
		margin-right: 20px;
		margin-bottom: 30px;
	}
	
	.file_class {
		width: 100px;
		height: 100px;
	}
</style>


