<template>
	<div class="">
		<!---->
		<div class="textarea-container">
			<textarea class="form-control custom-textarea" v-model="message"></textarea>
			<p class="token">total token : {{token}}</p>
			<el-button type="success" class="button" @click="UpdateFromAPI" :loading="loading"></el-button>
		</div>

		<MarkDown :markdown="result"></MarkDown>
	</div>
</template>

<script>
	import { baidu } from '../pagedata/baiduAPI/baiduApi'
	import MarkDown from '../pagedata/baiduAPI/MarkDown'

	//	const baidu = require('../pagedata/baiduAPI/baiduApi');
	export default {
		name: "Customize",
		data() {
			return {
				message: "AstroQueryVizierI/345/gaia2M3319",
				token: 0,
				result: "`astroquery``Vizier``I/345/gaia2`M3319\n\n`astroquery`\n\n```bash\npip install astroquery\n```\n\nPython\n\n```python\nfrom astropy import units as u\nfrom astropy.coordinates import SkyCoord\nfrom astroquery.vizier import Vizier\n\n# M3\nm3_center = SkyCoord(ra=15.8088*u.degree, dec=+34.0222*u.degree, frame='icrs')\n\n# 3\nsearch_radius = 0.05 * u.degree  # 3\n\n# Vizier\nvizier = Vizier(catalog=\"I/345/gaia2\")\n\n# \nvizier.ROW_LIMIT = -1  # \nquery_conditions = {\n    'Vmag': '<19',  # 19\n    'Pos': m3_center.to_string('hmsdms'),  # hmsdms\n    'SR': search_radius.to(u.arcmin).value  # \n}\n\n# \nresult_table = vizier.query_region(m3_center, radius=search_radius, **query_conditions)\n\n# \nprint(result_table)\n\n# astropyTablewrite\n# result_table.write('m3_gaia2_results.csv', format='csv')\n```\n\n****\n\n1. M3\n2. `Vizier``query_region``query_conditions``Vmag``I/345/gaia2`Vizier\n3. `vizier.ROW_LIMIT = -1`Vizier\n4. `SR`Vizier\n5. ",
				loading: false
			}
		},
		components: {
			MarkDown,
		},
		methods: {
			UpdateFromAPI() {
				console.log(this.message)
				this.loading = true
				const context = {
					"messages": [{
						"role": "user",
						"content": this.message
					}],
					"disable_search": false,
					"enable_citation": false
				}
				baidu(context, (result, token) => {
					this.result = result
					this.token += token
					this.loading = false
				})

			}
		}
	}
</script>

<style scoped>
	@font-face { 
		font-family: "calligraphy";
		/*src: url(../assets/font/chinese/chinese.font.ttf);*/
		src: url(../../assets/font/almm/AlimamaDongFangDaKai-Regular.ttf);
		/*src: url(../assets/font/pRAV9S9rN78N/cbpO6bHD763b.woff2);*/
	}
	
	.textarea-container {
		display: inline;
		position: relative;
	}
	
	.custom-textarea {
		width: 60%;
		height: 150px;
		padding: 10px;
		margin-bottom: 20px;
		border: 1px solid #ddd;
		border-radius: 4px;
		box-sizing: border-box;
		/* Ensure padding does not affect final width */
		font-size: 16px;
		letter-spacing: 2px;
		font-family: "calligraphy";
		resize: none;
		/* Disable resizing of textarea */
	}
	/* Custom styles for focused textarea */
	
	.custom-textarea:focus {
		border-color: #4A90E2;
		box-shadow: 0 0 8px rgba(74, 144, 226, 0.6);
		outline: none;
		/* Remove default browser outline */
	}
	
	.custom-textarea::-webkit-scrollbar{
     width: 3px ;
     height: 3px ;
	}
	.custom-textarea::-webkit-scrollbar-thumb {
	    border-radius: 1px ;
	      -moz-border-radius: 3px ;
	      -webkit-border-radius: 3px ;
	      background-color: #c3c3c3 ;
	}
	.custom-textarea:focus::-webkit-scrollbar-thumb{
		background-color: #8DADF2 ;
	}
	.custom-textarea::-webkit-scrollbar-track {
	    background-color: transparent ;
	}
	
	.button{
		position: absolute;
		/*top: 160px;
		right: 270px;*/
		bottom: 30px;
		right: 10px;
	}
	
	.token{
		position: absolute;
		/*top: 172px;
		right: 380px;*/
		bottom: 15px;
		right: 110px;
		color: #C3C3C3;
		font-size: 15px;
		z-index: 3;
	}
</style>



