<template>
	<div class="markdown-content" v-html="renderedMarkdown"></div>
</template>

<script>
	import markdownIt from 'markdown-it'
	import 'markdown-it-highlightjs' // markdown-ithighlight.js  
	import 'highlight.js/styles/default.css' // highlight.js  
	import mathjax from 'markdown-it-mathjax3';

	export default {
		props: {
			markdown: {
				type: String,
				required: true
			}
		},
		data() {
			return {
				renderedMarkdown: ''
			}
		},
		mounted() {
			this.renderMarkdown()
		},
		watch: {
			markdown(newVal) {
				this.renderMarkdown(newVal)
			}
		},
		methods: {
			renderMarkdown(markdownText = this.markdown) {
				const md = markdownIt({
						html: true, // HTML  
						linkify: true, //   
						typographer: true //   
					})
					.use(require('markdown-it-highlightjs'), {
						// highlight.js  
						//   
//						languages: ['javascript', 'python']  
					})
				
				md.use(mathjax)

				this.renderedMarkdown = md.render(markdownText)

				// highlightBlock  
				// markdown-it-highlightjs  
				//   
//				 document.querySelectorAll('pre code').forEach((block) => {  
//				   hljs.highlightBlock(block)  
//				 })  
			}
		}
	}
</script>

<style scoped>
	.markdown-content {
		/*  */
		font-family: 'Arial', sans-serif;
		font-size: 16px;
		line-height: 1.6;
		/*  */
		color: aliceblue;
		background-color: #8DADF2;
		/*  */
		padding: 20px;
		margin: 20px 40px 20px 40px;
		border-radius: 20px;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
		text-align: left;
		/*  */
		/* ... */
	}
</style>

<style>
	.markdown-content pre code {
		/*color: powderblue;*/
		background-color: seashell;
		border-radius: 20px;
	}
	
	/*   
   markdown-itHTMLCSS  
   markdown-it<h1><h6> */
	
	.markdown-content h1,
	.markdown-content h2,
	.markdown-content h3,
	.markdown-content h4,
	.markdown-content h5,
	.markdown-content h6 {
		/*  */
		margin-top: 20px;
		margin-bottom: 10px;
		font-weight: bold;
		/* ... */
	}
	
	.markdown-content p {
		/*  */
		margin-bottom: 15px;
		/* ... */
	}
	/* ... */
</style>


